@import "../../global.scss";


.intro{
    background-color: #44447c;
    display: flex;
    @include mobile{
        flex-direction: column;
        align-items: center;

    }

    .left{
       flex: 0.5;
       overflow: hidden;

       .imgContainer{

           width: 800px;
           height: 800px;
           background-color:#bcaaa4;
           box-shadow: inset 1em 1em 1rem #a1887f;
        //    box-shadow: 10px 5px 5px #f71945;

           border-radius: 50%;
           display: flex;
           align-items: flex-end;
           justify-content: center;
           float: right;

           @include mobile{
            align-items: flex-start;
            width: 700px;
            height: 700px;
           }

           img{
            height: 95%;
           

            @include mobile{
             height: 90%;
            }
         }
          
       }
       
     
    }

    .right{
        flex: 0.5;
        position: relative;

        .wrapper{

            width: 100%;
            height: 100%;
            padding-left:50px;
            display: flex;
            flex-direction: column;
            justify-content: center;

            @include mobile{
                padding-left:0;
                align-items: center;
        
            }

            h1{
                font-size: 65px;
                margin: 10px 0;
                color: white;

                @include mobile{
                   font-size: 40px;
                }
            }
            h2{ 
                font-size: 35px;
                color: white;
            }
            h3{
                font-size: 30px;
                color: white;
                
                @include mobile{
                    font-size: 20px;
                 }

                span{
                    font-size: inherit;
                    color:#ff1744;

                }
                .ityped-cursor {       
                    animation: blink 1s infinite;
                }     
                @keyframes blink {
                    50%{
                        opacity: 1;
                    }
                    100% {
                        opacity: 0;
                    }
                }
            }



        }
        a{
           position: absolute;
           bottom:10px;
           left: 40%;

           img{
               width: 30px;
               animation: arrowBlink 2s infinite;
           }
           @keyframes arrowBlink {
               100%{
                   opacity:0;
               }
           }

        }
        

    }
}